import {useNavigate} from 'react-router-dom';
import {useEffect} from "react";

const Home = ({isLoggedIn}) => {
    const navigate = useNavigate();

    useEffect(()=>{
        if (!isLoggedIn){
            navigate('/login')
        }
    },[isLoggedIn])

    return(
        <h1>Hello</h1>
    )
}

export default Home;